<template>
  <div id="tabbatitem" @click="itemclick">
    <div :class="{active:isactive}"><slot></slot></div>  
  </div>
</template>

<script>
export default {
    props:{
        path:String
    },
  
methods:{
    itemclick(){
 this.$router.replace(this.path)
    }
},
computed:{
    isactive(){
        return this.$route.path.indexOf(this.path)!==-1
    }
}
}
</script>

<style>
#tabbatitem{
    flex: 1;
    float: left;
    line-height: 49px;
    color: white;
    text-align: center;
}
.active{
    color:yellow;
    font-style: italic;
}
</style>